<template><section><h1>CylinderGeometry, CylinderOutlineGeometry</h1>
<ul>
<li>The <code v-pre>vc-geometry-cylinder</code> component is used to load a geometry that describes a cylinder.</li>
<li>The <code v-pre>vc-geometry-outline-cylinder</code> component is used to load a geometry that describes a cylinder outline.</li>
<li>It needs to be used as a sub-component of <code v-pre>vc-instance-geometry</code>, can be mounted on <code v-pre>vc-primitive</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load CorridorGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes" :modelMatrix="modelMatrix">
            <vc-geometry-cylinder ref="cylinder" :length="400000.0" :topRadius="200000.0" :bottomRadius="200000.0" :slices="1024" :vertexFormat="vertexFormat"></vc-geometry-cylinder>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes" :modelMatrix="modelMatrixOutline">
            <vc-geometry-outline-cylinder ref="cylinderOutline" :length="400000.0" :topRadius="200000.0" :bottomRadius="200000.0" :slices="1024" :numberOfVerticalLines="128"></vc-geometry-outline-cylinder>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
